import React from "react";
import styled, { keyframes } from "styled-components";

const CustomeLoader = () => {
  const rotate360 = keyframes`
    from {
    transform: rotate(0deg);
    }
    to {
    transform: rotate(360deg);
    }
    `;

  const Spinner = styled.div`
    margin: 16px;
    animation: ${rotate360} 1s linear infinite;
    transform: translateZ(0);
    border-top: 2px solid #1D718B ;
    border-right: 2px solid #1D718B ;
    border-bottom: 2px solid #1D718B ;
    border-left: 4px solid linear-gradient(to right, #8360c3, #2ebf91);
    background: transparent;
    width: 100px;
    height: 100px;
    border-radius: 50%;
  `;


  return (
    <div style={{ padding: "24px" }}>
      <Spinner />
      <div style={{ marginLeft: "20px" ,color:"#1D718B " }}>
        <b>Loading...</b>
      </div>
    </div>
  );
};

export default CustomeLoader;
