  const handleLawyerP = (e) => {
    console.log(e,"dsnfhdsjf")
    // setSwitchLawyer(!switchLawyer)
    // alert(e)
    // alert("nice")
    // setToggled(!isToggled);
    
    axios
      .get("addEditPremium/"+e, {
        headers: {
          Accept: "application/json",
          Authorization: "Bearer " + token,
        },
      })
      .then((res) => {
        axios
        .get("lawyerListforAdmin", {
          headers: {
            Accept: "application/json",
            Authorization: "Bearer " + token,
          },
        })
        .then((res) => {
          res.data.status == 200 ?   setLawyer(res.data.data):  setLawyer([])
          // setLawyer(res.data.data);
          console.log("Lawyer", res.data.data);
        });
       
        // window.location.reload();
        // setToggled(!isToggled);
        Toast.fire({
          icon: "success",
          title: res.data.message,
        });
      })
      .catch((error) => {
        console.log(error);
      });
   
  }


  useEffect(() => {
    axios
      .get("lawyerListforAdmin", {
        headers: {
          Accept: "application/json",
          Authorization: "Bearer " + token,
        },
      })
      .then((res) => {
        res.data.status == 200 ?   setLawyer(res.data.data):  setLawyer([])
        // setLawyer(res.data.data);
        console.log("Lawyer", res.data.data);
      });
  }, [token]);

  // ---------------------------------------------------------------------------------------------------------------------
  const Toast = Swal.mixin({
    toast: true,
    position: "top-end",
    showConfirmButton: false,
    timer: 3000,
    timerProgressBar: true,
    didOpen: (toast) => {
      toast.addEventListener("mouseenter", Swal.stopTimer);
      toast.addEventListener("mouseleave", Swal.resumeTimer);
    },
    // title: "Lawyer Added As Premium",
  });
  
  

  const handleStatus = (e, row) => {
    e.preventDefault();
    const helpdetailsobj = new FormData();
    helpdetailsobj.append("id", row.id);
    helpdetailsobj.append("status", e.target.value);
    axios
      .post("UpdateKycStatus", helpdetailsobj, {
        headers: {
          Accept: "application/json",
          Authorization: "Bearer " + token,
        },
      })
      .then((result) => {
        Toast.fire({
          icon: "success",
          title: result.data.message,
        });
        axios
          .get("lawyerListforAdmin", {
            headers: {
              Accept: "application/json",
              Authorization: "Bearer " + token,
            },
          })
          .then((res) => {
            setLawyer(res.data.data);
            console.log("Lawyer", res.data.data);
          });
      })
      .catch((error) => {
        Toast.fire({
          icon: "error",
          title: error.response.data.message,
        });
      });
  };

  // const handleActiveStatus = (e, row) => {
  //   const helpdetailsobj = new FormData();
  //   helpdetailsobj.append("id", row.id);
