  //         })
  //         .then((res) => {
  //           // setLawyer(res?.data?.data);
  //           console.log(res?.data?.data);
  //         });
  //     })
  //     .catch((error) => {
  //       Toast.fire({
  //         icon: "error",
  //         title: error.response.data.message,
  //       });
  //     });
  // };
  // --------------------------------------------------DELETE Lawyer---------------------------------------------

  // const deleteHandler = (row) => {
  //   axios
  //     .delete("/api/delete_admin_preference/" + row.id, {
